const Slider= (($) => {


	/**
	 * ------------------------------------------------------------------------
	 * Constants
	 * ------------------------------------------------------------------------
	 */

	const NAME                     = 'slider'
	const DATA_KEY                 = 'slider'
	const EVENT_KEY                = `.${DATA_KEY}`
	const DATA_API_KEY             = '.data-api'
	const JQUERY_NO_CONFLICT       = $.fn[NAME]
	const ESCAPE_KEYCODE           = 27 // KeyboardEvent.which value for Escape (Esc) key
	const ARROW_UP_KEYCODE         = 38 // KeyboardEvent.which value for up arrow key
	const ARROW_DOWN_KEYCODE       = 40 // KeyboardEvent.which value for down arrow key
	const RIGHT_MOUSE_BUTTON_WHICH = 3 // MouseEvent.which value for the right button (assuming a right-handed mouse)

	const Event = {
		HIDE             : `hide${EVENT_KEY}`,
		HIDDEN           : `hidden${EVENT_KEY}`,
		SHOW             : `show${EVENT_KEY}`,
		SHOWN            : `shown${EVENT_KEY}`,
		CLICK            : `click${EVENT_KEY}`,
		CLICK_DATA_API   : `click${EVENT_KEY}${DATA_API_KEY}`,
		KEYDOWN_DATA_API : `keydown${EVENT_KEY}${DATA_API_KEY}`
	}

	const ClassName = {
		BACKDROP : 'dropdown-backdrop',
		DISABLED : 'disabled',
		OPEN     : 'open'
	}

	const Selector = {
		BACKDROP      : '.dropdown-backdrop',
		DATA_TOGGLE   : '[data-toggle="dropdown"]',
		FORM_CHILD    : '.dropdown form',
		ROLE_MENU     : '[role="menu"]',
		ROLE_LISTBOX  : '[role="listbox"]',
		NAVBAR_NAV    : '.navbar-nav',
		VISIBLE_ITEMS : '[role="menu"] li:not(.disabled) a, '
		+ '[role="listbox"] li:not(.disabled) a'
	}


	/**
	 * ------------------------------------------------------------------------
	 * Class Definition
	 * ------------------------------------------------------------------------
	 */

	class Slider {

		constructor(element) {
			this.line = $(element)
			this.dragger = $(element).children()

			console.log('constr')
			console.log(this.line)
			console.log(this.dragger)



			this.dragger.on('mousedown', (e)=>{
				console.log('mousedown')
				console.log(e.pageX)

				let draggerCoords = this.getCoords(this.dragger)
				console.log(draggerCoords)

				let  shiftX = e.pageX - draggerCoords.left;
				console.log(shiftX)

				let sliderCoords = this.getCoords(this.line)
		/*		let draggerLeft = parseInt(this.dragger.css('left'))
				 console.log('draggerLeft')
				 console.log(draggerLeft)*/

				$(this.line).on('mousemove', (e)=>{
					//  вычесть координату родителя, т.к. position: relative
					console.log(e.pageX)
					var newLeft = e.pageX - shiftX - sliderCoords.left + this.dragger.width()/2
					console.log('newLeft')
					console.log(newLeft)

					// курсор ушёл вне слайдера
					if (newLeft < 0) {
						newLeft = 0
					}
					let rightEdge = this.line.width() -4

					console.log('rightEdge')
					console.log(rightEdge)

					if (newLeft > rightEdge) {
						newLeft = rightEdge
					}

					this.dragger.css('left',newLeft + 'px')

				})
				this.line.on('mouseup', (e)=>{
					console.log('mouseup')
					this.line.off('mousemove')
					this.line.off('mouseup')
				})
			})

		}


		getCoords(elem) {
			let box = elem[0].getBoundingClientRect()

			return {
				top: box.top + pageYOffset,
				left: box.left + pageXOffset
			}

		}


		// getters

		static get VERSION() {
			return VERSION
		}


		// public

		toggle() {
			if (this.disabled || $(this).hasClass(ClassName.DISABLED)) {
				return false
			}

			let parent   = Dropdown._getParentFromElement(this)
			let isActive = $(parent).hasClass(ClassName.OPEN)

			Dropdown._clearMenus()

			if (isActive) {
				return false
			}

			if ('ontouchstart' in document.documentElement &&
				(!$(parent).closest(Selector.NAVBAR_NAV).length)) {

				// if mobile we use a backdrop because click events don't delegate
				let dropdown       = document.createElement('div')
				dropdown.className = ClassName.BACKDROP
				$(dropdown).insertBefore(this)
				$(dropdown).on('click', Dropdown._clearMenus)
			}

			let relatedTarget = { relatedTarget : this }
			let showEvent     = $.Event(Event.SHOW, relatedTarget)

			$(parent).trigger(showEvent)

			if (showEvent.isDefaultPrevented()) {
				return false
			}

			this.focus()
			this.setAttribute('aria-expanded', 'true')

			$(parent).toggleClass(ClassName.OPEN)
			$(parent).trigger($.Event(Event.SHOWN, relatedTarget))

			return false
		}

		dispose() {
			$.removeData(this._element, DATA_KEY)
			$(this._element).off(EVENT_KEY)
			this._element = null
		}


		// private

		_addEventListeners() {
			$(this._element).on(Event.CLICK, this.toggle)
		}


		// static

		static _jQueryInterface(config) {
			return this.each(function () {
				console.log('initfffffffff')
				let data  = $(this).data(DATA_KEY)

				if (!data) {
					$(this).data(DATA_KEY, (data = new Slider(this)))
				}

				/*if (typeof config === 'string') {
					if (data[config] === undefined) {
						throw new Error(`No method named "${config}"`)
					}
					data[config].call(this)
				}*/
			})
		}



		static _getParentFromElement(element) {
			let parent
			let selector = Util.getSelectorFromElement(element)

			if (selector) {
				parent = $(selector)[0]
			}

			return parent || element.parentNode
		}


	}



	/**
	 * ------------------------------------------------------------------------
	 * jQuery
	 * ------------------------------------------------------------------------
	 */

	$.fn[NAME]             = Slider._jQueryInterface
	$.fn[NAME].Constructor = Slider
	$.fn[NAME].noConflict  = function () {
		$.fn[NAME] = JQUERY_NO_CONFLICT
		return Slider._jQueryInterface
	}

	return Slider

})(jQuery)

export default Slider




